import React, { useState, useEffect } from 'react';
import Welcome from './components/Welcome';
import Meals from './components/Meals';

function App(props) {

	const [ steps, setSteps ] = useState('welcome');

	useEffect(() => {
		setTimeout(() => {
			setSteps('meals');
		}, 3000);
	});

	if(steps === "welcome") {
		return (
			<Welcome></Welcome>
		)
	}

	if(steps === "meals") {
		return (
			<Meals></Meals>
		)
	}

	return (
		<></>
	);
}

export default App;
