import React, { useState, useEffect } from 'react';
import classNames from 'classnames';
import api from '../services/api';

function Meals(props) {

	const day = new Date().getDay();
	const listDays = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'];

	const [ days, setDays ] = useState([]);
	const [ weekDay, setWeekDay ] = useState('');
	const [ activeIndex, setActive ] = useState(day-1);

	useEffect(() => {
		switch (day) {
			case 1:
				setWeekDay('Mon');
				break;
			case 2: 
				setWeekDay('Tue')
				break;
			case 3:
				setWeekDay('Wed');
				break;
			case 4:
				setWeekDay('Thu');
				break;
			case 5:
				setWeekDay('Fri');
				break;
			default:
				return null;
		}

		async function loadMeals() {
			const response = await api.get('/list');
			setDays(response.data);
		}
		loadMeals();

		console.log(day)
	}, []);

	return (
		<div className="App">
			<div className="top_bar">What's Cooking?</div>
			<ul className="nav">
				{days.map((list, i)=> (
					<li
						key={i}
						onClick={e => setActive(i)}
						className={classNames('', i === activeIndex ? "active" : "", list.week_day === weekDay ? "current_day": "", i+1 < day ? "past_day" : "")}>
						{list.week_day}
					</li>
				))}
			</ul>

			{days.map((day, i) => (
				<div key={day._id} className={classNames('day', listDays[activeIndex] === day.week_day ? "active" : "")}>
					{/* Meat Lovers */}
					<div className="meat_lovers">
						<h4 className="meat_lovers">Meat lovers</h4>
						<p>{day.meals.meat_lovers.m_ingredients}</p>
					</div>

					{/* Vegetarian */}
					<div className="vegetarian">
						<h4 className="vegetarian">Vegetarian</h4>
						<p>{day.meals.vegetarian.v_ingredients}</p>
					</div>

					{/* Salad */}
					<div className="salad">
						<h4 className="salad">Salad</h4>
						<p>{day.meals.salad}</p>
					</div>
				</div>
			))}
		</div>
	)
}

export default Meals;